import {createArticles} from '@/api/article'
export default {
  middleware:'authenticated',
  name: "editorIndex",
  data(){
    return{
      article:{
        title:'',
        description:'',
        body:'',
        tagList:''
      }
    }
  },
  methods:{
   async onSubmit(){
     const status= this.isEmpty(this.article)
      if(!status){
        alert("请核实内容后在提交")
        return false;
      }
      const _data={
        ...this.article,
        tagList:this.article.tagList.split(",")
      }
      await createArticles(_data).then(()=>{
        this.$router.push('/')
      })
   },
    isEmpty(obj){
      let res=1
      Array.from(Object.entries(obj)).forEach((val)=>{
        res&=!!val[1]
      })
      return !!res
    }
  }
}
